import React, { Component } from 'react';
import { Card, CardHeader, Col, Row, Button, Form, Input, FormGroup, Label, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import commonService from '../../../core/services/commonService';

import Loader from '../../Loader/Loader';
import DegreeData from './DegreeData';
import './Degree.css'

class Degree extends Component {
  constructor(props){
    super(props);
    this.state = {
      modal: false,
      degreeName: "",
      degreeList: [],
      loading: true,
      rowIndex: -1,
      status: "",
      errors: {degreeName: '', status: '', error: ''},
      filterItem: {},

    } 
    this.handleEditCategory = this.handleEditCategory.bind(this);
    this.submitHandler = this.submitHandler.bind(this);
    this.handleDeleteCategory = this.handleDeleteCategory.bind(this);
    this.filterItemList = this.filterItemList.bind(this);
    this.resetSearchFilter = this.resetSearchFilter.bind(this);
  }

  // Fetch the category List
  componentDidMount() { 
    this.degreeList();
  }
  /*Category List API*/
  degreeList(filterItem = {}) {
    let filterParmas = "";
    filterParmas = "?isSuperSpecialization="+false;
    if(filterItem.keyword !== undefined && filterItem.keyword !== "" ) 
      filterParmas += (filterParmas !=="" ) ? "&keyword="+filterItem.keyword: "?keyword="+filterItem.keyword;
    if(filterItem.status !== undefined && filterItem.status !== "" ) {
      let statusText = 1;
      if(filterItem.status === "Inactive")
        statusText = 0;
      else if(filterItem.status === "Pending")
        statusText = 2;
      else if(filterItem.status === "Under Negotiation")
        statusText = 3;
      filterParmas += (filterParmas !=="" ) ? "&status="+statusText: "?status="+statusText;
    }
    this.setState( { loading: true}, () => {
      commonService.getAPIWithAccessToken('degree'+filterParmas)
        .then( res => {
          
           
          if ( undefined === res.data.data || !res.data.status ) {
            this.setState( {  loading: false } );
            toast.error(res.data.message);    
            return;
          }   

          this.setState({loading:false, degreeList: res.data.data});     
         
        } )
        .catch( err => {   
                
          if(err.response !== undefined && err.response.status === 401) {
            localStorage.clear();
            this.props.history.push('/login');
          }
          else {
            this.setState( { loading: false } );
            toast.error(err.message);    
          }
        } )
    } )
  }
  submitHandler (event) {
    event.preventDefault();
    event.target.className += " was-validated";
    if(!this.validateForm()){
      return false;
    }
    let formData = {};
    formData.degreeName = this.state.degreeName;
    let statusTextValue = this.state.status; 
    let statusText = 1;
    if(statusTextValue === "Inactive")
      statusText = 0;
    else if(statusTextValue === "Pending")
      statusText = 2;
    else if(statusTextValue === "Under Negotiation")
      statusText = 3;
    formData.status = statusText;
    
    this.setState( { loading: true}, () => {
      if(this.state.rowIndex > -1){
        /* Update Category */
        const degreeId = this.state.degreeList[this.state.rowIndex].degreeId;
        formData.degreeId = degreeId;
        
        commonService.putAPIWithAccessToken( `degree`, formData)
          .then( res => {
            
           
            
            if ( undefined === res.data.data || !res.data.status ) {
              let errors = {};
              errors.degreeName = res.data.message;
              this.setState({loading: false, errors: errors});   
              //toast.error(res.data.message);             
              return;
            } 
            let updateddegreeLists = this.state.degreeList;
            updateddegreeLists[this.state.rowIndex] = res.data.data;
            this.setState({modal: false, rowIndex: "", loading:false, errors : {degreeName: "", status: ""}});
            toast.success(res.data.message);    
            this.degreeList();
          } )
          .catch( err => {       
              
            if(err.response !== undefined && err.response.status === 401) {
              localStorage.clear();
              this.props.history.push('/login');
            }
            else {
              this.setState( { loading: false } );
              toast.error(err.message);    
            }
          } )
       
      }
      else{
         /* Add Category */
        commonService.postAPIWithAccessToken( `degree`, formData)
          .then( res => {
            
            if ( undefined === res.data.data || !res.data.status ) {
              let errors = {};
              errors.degreeName = res.data.message;
              this.setState({loading: false, errors: errors});   
              //toast.error(res.data.message);             
              return;
            }           
            this.setState({modal: false, loading: false});
            toast.success(res.data.message); 
            this.degreeList();
          } )
          .catch( err => {
            if(err.response !== undefined && err.response.status === 401) {
              localStorage.clear();
              this.props.history.push('/login');
            }
            else{
              this.setState( { loading: false } );
              toast.error(err.message); 
            }
          } )
      }
    })
  };
  /* Input Field On changes*/
  changeHandler = event => {
    const name = event.target.name;
    const value = event.target.value
    this.setState({ [name]: value });
  };
  
  /* Validate Form */
  validateForm() {
    let formValid = true;
    let formField = this.state; 
    let errors = {};
    if(formField.degreeName === undefined || formField.degreeName.trim() === "" ){
      formValid = false;
      errors['degreeName'] = 'Degree name field required';
    }
    if(formField.status === undefined || formField.status.trim() === "" ){
      formValid = false;
      errors['status'] = 'Status field required';
    }
    this.setState({errors:  errors});
    return formValid;
    
  }
  /* Set Error Class*/
  errorClass(error) {
    return(error.length === 0 ? '' : 'has-error');
  }

  toggle = () => {
    this.setState({
      modal: !this.state.modal,
      degreeName: "",
      status: "",
      errors: {},
      rowIndex: -1,      
    });
  }
  

  /* Edit Category*/
  handleEditCategory(rowIndex){
   
      const degreeItem = this.state.degreeList[rowIndex];
      let status = "Active"
      if(degreeItem.status === 0)
        status = "Active";
      else if(degreeItem.status === 2)
        status = "Pending";
      else if(degreeItem.status === 3)
        status = "Under Negotiation";

      this.setState({modal: true, status: status, degreeName: degreeItem.degreeName, remark: degreeItem.remark, rowIndex: rowIndex, formValid: true, degreeName_valid: true, categoryImageUrl: degreeItem.imagUrl});
  }
  /* Add category */
  handleDeleteCategory(rowIndex){
   
    const degreeItem = this.state.degreeList[rowIndex];
   
    this.setState( { loading: true}, () => {
      commonService.deleteAPIWithAccessToken( `degree/`+degreeItem.degreeId)
        .then( res => {
          this.setState({loading: false});
          if ( undefined === res.data || !res.data.status ) {            
             toast.error(res.data.message);      
            return;
          }         
          
          toast.success(res.data.message);
          this.degreeList();
        } )
        .catch( err => {       
            
          if(err.response !== undefined && err.response.status === 401) {
            localStorage.clear();
            this.props.history.push('/login');
          }
          else{
            this.setState( { loading: false } );
            toast.error(err.message);
          }
      } )
    })
  }

  changeFilterHandler = event => {
    const name = event.target.name;
    const value = event.target.value;
    const filterItem = this.state.filterItem
    filterItem[name] = value;
    this.setState({ filterItem: filterItem });
  };

  filterItemList(){
    const filterItem = this.state.filterItem;
    this.degreeList(filterItem);
  }

  resetSearchFilter() {
    this.setState({filterItem: { keyword: '', status: ''}});
    this.degreeList();
  }

  render() {

    const { degreeList, loading, modal, errors  } = this.state;     
    let loaderElement ='';
    
    if(loading)
      loaderElement = <Loader />
    /*let categoryImagePreview = '';
    if(this.state.categoryImageUrl !== "")
      categoryImagePreview = <div className="imagePreview"><img src={this.state.categoryImageUrl} alt="" /></div>*/

    return (
      <div className="animated fadeIn">
        <Row>
          
          {loaderElement}
          <Col lg={12}>
            <Card className="oep-card ">
                <CardHeader className="mainHeading">
                  <strong>Degree List</strong> 
                  <Button color="" className="categoryAdd" type="button" onClick={this.toggle}><i className="fa fa-plus"></i> Add Degree</Button>
                </CardHeader>
                <Row>
                  <Col md={12}>
                    <div className="search-filter">
                    <Row>                      
                      
                      <Col md={"3"}>
                        <FormGroup> 
                          <Label htmlFor="filter_doctor_id">Search By Name</Label>            
                          <Input type="text" placeholder="Search By Name" id="keyword" name="keyword" value={this.state.filterItem.keyword} onChange={this.changeFilterHandler} />
                        </FormGroup>  
                      </Col>
                      <Col md={"3"}>
                        <FormGroup> 
                          <Label htmlFor="filter_doctor_id">Search By Status</Label>            
                          <Input type="select" placeholder="Search By Status" id="status" name="status" value={this.state.filterItem.status} onChange={this.changeFilterHandler}>
                              <option value = "">Select</option>
                              <option value="Active">Active</option>
                              <option value="Inactive">Inactive</option>
                          </Input>
                        </FormGroup>  
                      </Col>
                      <Col md={"3"}>
                        <FormGroup className="filter-button-section"> 
                          <Button className="search-btn" type="button" onClick={this.filterItemList}>Search</Button> 
                          <Button className="search-btn" id="resetButton" type="button" onClick={this.resetSearchFilter}>Reset</Button> 
                        </FormGroup>             
                      </Col>
                    </Row>
                     </div>  
                  </Col>       
                  <Col lg= {12}>   
                    <DegreeData data={degreeList} toggle={this.toggle} editCategoryAction={this.handleEditCategory} deleteCategoryAction={this.handleDeleteCategory} dataTableLoadingStatus = {this.state.loading} />
                  </Col> 
                </Row>
            </Card>
          </Col>
        </Row>
        <Modal isOpen={modal} toggle={this.toggle} className="category-modal-section">
          <ModalHeader toggle={this.toggle}>Degree</ModalHeader>
          <Form onSubmit={this.submitHandler} noValidate>
            <ModalBody>
              
              <FormGroup> 
                <Label htmlFor="degreeName">Degree Name</Label>            
                <Input type="text" placeholder="Degree Name *" invalid={errors['degreeName'] !== undefined && errors['degreeName'] !== ""} id="degreeName" name="degreeName" value={this.state.degreeName} onChange={this.changeHandler} required />
                <div className="invalid-feedback">
                  {errors['degreeName']}
                </div>
              </FormGroup>
              
              <FormGroup>
                <Label htmlFor="template_status">Status</Label>            
                <Input type="select" placeholder="Status *" id="status" invalid={errors['status'] !== undefined && errors['status'] !== ""} name="status" value={this.state.status} onChange={this.changeHandler} required >
                  <option value="">Select Status</option>
                  <option value="Active">Active</option>
                  <option value="Inactive">Inactive</option>
                  
                </Input>
                <div className="invalid-feedback">
                  {errors['status']}
                </div>
              </FormGroup>
               
            </ModalBody>
            <ModalFooter>
              <Button color="primary" disabled={this.state.loading} type="submit">Submit</Button>
              <Button color="secondary" onClick={this.toggle}>Cancel</Button>
            </ModalFooter>
          </Form>
        </Modal>
      </div>

    )
  }
}

export default Degree;
